<template>
  <div class="middle">
    <h1 class="label">New Year Countdown 2021
    </h1>
    <div class="time">
      <span>
						<div id="d">
              <img class="editgif" :src="gdl" alt="Smiley face">
              <img class="editgif fl" :src="gdr" alt="Smiley face">
              <img v-if="gd3" class="editgif fl" :src="gd3" alt="Smiley face">
            </div>
						Days
					</span>
      <span>
						<div id="h">
              <img class="editgif" :src="ghl" alt="Smiley face">
              <img class="editgif fl" :src="ghr" alt="Smiley face">
            </div>
						Hours
					</span>
      <span>
						<div id="m">
              <img class="editgif" :src="gml" alt="Smiley face">
              <img class="editgif fl" :src="gmr" alt="Smiley face">
            </div>
						Minutes
					</span>
      <span>
        <div id="s">
          <img class="editgif" :src="gsl" alt="Smiley face">
          <img class="editgif fl" :src="gsr" alt="Smiley face">
        </div>
        Seconds
      </span>
    </div>
  </div>
</template>

<script>
import { timeout } from 'q';
import s1 from '@/assets/img/s1.gif'
import ss0 from '@/assets/img/ss0.gif'
export default {
  name: 'CountTime',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      gd3: null,
      gdr: s1,
      gdl: s1,
      ghr: s1,
      ghl: s1,
      gsr: s1,
      gsl: s1,
      gml: s1,
      gmr: s1,
    }
  },
  created: function (){
    const self = this
    setInterval(() => {
      const timenow = new Date()
      self.tinhTime(timenow)
    }, 1000);
  },
  methods: {
    tinhTime (timeNow) {
      let timenow = new Date()
      let getGiaoThua = new Date('2021-02-12 00:00:00')
      let getDiff = getGiaoThua - timeNow
      if (getDiff <= 0) {
        this.countDown(getDiff)
      }else{
        let newDate = getDiff / (1000*24*3600)
        console.log(Number.parseInt(newDate))
        const dr = this.addRight(Number.parseInt(newDate))
        const dl = this.addLeft(Number.parseInt(newDate))
        const gd3 = this.addDay3(Number.parseInt(newDate))

        this.gdr = require('@/assets/img/ss'+ dr + '.gif')
        this.gdl = require('@/assets/img/ss'+ dl + '.gif')
        if (gd3 != '') {
          this.gd3 = require('@/assets/img/ss'+ gd3 + '.gif')
        }

        this.ghr = require('@/assets/img/ss'+ this.addRight(23 - timeNow.getHours()) + '.gif')
        this.ghl = require('@/assets/img/ss'+ this.addLeft(23 - timeNow.getHours()) + '.gif')

        this.gsr = require('@/assets/img/ss'+ this.addRight(59 - timeNow.getSeconds()) + '.gif')
        this.gsl = require('@/assets/img/ss'+ this.addLeft(59 - timeNow.getSeconds()) + '.gif')

        this.gmr = require('@/assets/img/ss'+ this.addRight(59 - timeNow.getMinutes()) + '.gif')
        this.gml = require('@/assets/img/ss'+ this.addLeft(59 - timeNow.getMinutes()) + '.gif')
      }
    },
    addLeft (time) {
      return time < 10 ? '0' : time.toString()[0]
    },
    addRight (time) {
      return time < 10 ? time : time.toString()[1]
    },
    addDay3 (day) {
      let strDay = day.toString()
      return strDay.length == 3 ? strDay[2] : ''
    },
    countDown (getDiff) {
      this.gdr = ss0,
      this.gdl = ss0,
      this.ghr = ss0,
      this.ghl = ss0,
      this.gsr = ss0,
      this.gsl = ss0,
      this.gml = ss0,
      this.gmr = ss0
    }
  }
}
</script>



